import { Component, Directive, OnInit, ViewContainerRef, Inject, forwardRef, Input, EventEmitter, ViewRef, booleanAttribute, Output, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { Subscription, startWith } from 'rxjs';


@Directive({
  selector: '[mrdTabBodyHost]'
})
export class MatTabBodyPortal implements OnInit {
  /** Subscription to events for when the tab body begins centering. */
  private _centeringSub = Subscription.EMPTY;
  /** Subscription to events for when the tab body finishes leaving from center position. */
  private _leavingSub = Subscription.EMPTY;

  constructor(
    private viewContainerRef: ViewContainerRef,
    @Inject(forwardRef(() => MrdTabBodyComponent)) private _host: MrdTabBodyComponent,
  ) {}

  /** Set initial visibility or set up subscription for changing visibility. */
  ngOnInit(): void {
    this._centeringSub = this._host._goingToActive
      .pipe(startWith(this._host.active))
      .subscribe((isCentering: boolean) => {
        if (isCentering && this.viewContainerRef.length === 0) {
          this.viewContainerRef.insert(this._host.content);
        }
      });

    this._leavingSub = this._host._isHidden.subscribe(() => {
      if (!this._host.preserveContent) {
        this.viewContainerRef.clear();
      }
    });
  }

  /** Clean up centering subscription. */
  ngOnDestroy(): void {
    this._centeringSub.unsubscribe();
    this._leavingSub.unsubscribe();
  }
}


@Component({
  selector: 'mrd-tab-body',
  templateUrl: './mrd-tab-body.component.html',
  styleUrls: ['./mrd-tab-body.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MrdTabBodyComponent {

  @Input('content') public set content(value: ViewRef) {
    this._content = value;
    this.cdr.detectChanges();
  }
  public get content(): ViewRef {
    return this._content;
  }

  private _content: ViewRef;

  @Input() public set index(value: number) {
    this._index = value;
    this.cdr.detectChanges();
  }
  public get index(): number {
    return this._index;
  }
  private _index: number;

  @Input() public set active(value: boolean) {
    this._active = value;
    this.cdr.detectChanges();
  }
  public get active(): boolean {
    return this._active;
  }
  private _active: boolean;

  @Input({transform: booleanAttribute}) public preserveContent: boolean = false;

  private hideTimeout: any;
  @Output() _isHidden: EventEmitter<boolean> = new EventEmitter();
  @Output() _goingToActive: EventEmitter<boolean> = new EventEmitter();

  public set translate(value: number|string) {
    if (value !== 0) {
      clearTimeout(this.hideTimeout);
      this.hideTimeout = setTimeout(() => {
        this.hidden = true;
        this._isHidden.emit(true);
        console.log(this.index, 'hide')
        this.cdr.detectChanges();
      }, 1000);
      this._translate = value+'%';
    } else {
      clearTimeout(this.hideTimeout);
      this.hidden = false;
      this._goingToActive.emit(true);
      setTimeout(() => {
        this._translate = value+'%';
        this.cdr.detectChanges();
      }, 1);
    }

    this.cdr.detectChanges();
  }
  public get translate(): string {
    return this._translate;
  }
  private _translate: string = '0%';

  @Input() public set hidden(value: boolean) {
    this._hidden = value;
    this.cdr.detectChanges();
  }
  public get hidden(): boolean {
    return this._hidden;
  }
  private _hidden: boolean = true;

  constructor(
    private cdr: ChangeDetectorRef
  ) {}
}
