import { BaseObject, ObservableValue, SubscriptionHandler, Util } from 'mrd-core';
import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ContentChildren, ElementRef, EventEmitter, Input, Output, QueryList, ViewChildren, booleanAttribute } from '@angular/core';
import { MrdTabComponent } from '../mrd-tab/mrd-tab.component';
import { ActivatedRoute, Router } from '@angular/router';
import { MrdTabBodyComponent } from '../mrd-tab-body/mrd-tab-body.component';

@Component({
  selector: 'mrd-tab-group',
  templateUrl: './mrd-tab-group.component.html',
  styleUrls: ['./mrd-tab-group.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MrdTabGroupComponent extends BaseObject implements AfterViewInit {

  @ContentChildren(MrdTabComponent) tabs: QueryList<MrdTabComponent>;
  @ViewChildren(MrdTabBodyComponent) tabBodys: QueryList<MrdTabBodyComponent>;
  @ViewChildren('mrdTabHeader') tabHeader: QueryList<ElementRef>;

  @Input({transform: booleanAttribute}) public rounded: boolean = false;
  @Input({transform: booleanAttribute}) public labelsFit: boolean = false;
  @Input({transform: booleanAttribute}) public noHeader: boolean = false

  @Input() public queryParameterActive: string;

  @Input() public set headerHeight(value: number) {
    this._headerHeight = value;
    this.cdr.detectChanges();
  }
  public get headerHeight(): number {
    return this._headerHeight;
  }
  private _headerHeight: number;

  @Input() public set selectedIndex(value: number) {
    this._selectedIndex = value;
    this.activateTab(value);
  }

  public get selectedIndex(): number {
    return this._selectedIndex;
  }
  private _selectedIndex: number;


  @Input() public changeTab: ObservableValue<number>;

  @Output() public selectedTabChanged: EventEmitter<MrdTabComponent> = new EventEmitter();
  @Output() public selectedTabIndexChanged: EventEmitter<number> = new EventEmitter();

  public tabUnderlineLeft: number = 0;
  public tabUnderlineWidth: number = 0;

  private activeIndex: number;
  private activeTab: MrdTabComponent;
  private activeTabBody: MrdTabBodyComponent;

  constructor(
    private cdr: ChangeDetectorRef,
    private router: Router,
    private route: ActivatedRoute
  ) {
    super();
  }

  ngAfterViewInit(): void {
    this.tabs.forEach((tab: MrdTabComponent, index: number) => {
      tab.index = index;
    });

    if (this.queryParameterActive) {
      this.route.queryParams.subscribe((params) => {
        if (Util.isDefined(params[this.queryParameterActive])) {
          let index = parseInt(params[this.queryParameterActive]);
          if (index >= 0 && index < this.tabs.length) {
            this.activateTab(index);
          }
        } else {
          this.activateTab(0);
        }
      });
    } else {
      this.activateTab(0);
    }

    if (Util.isDefined(this.changeTab)) {
      this.watch(this.changeTab.changed, new SubscriptionHandler((index: number) => {
        this.activateTab(index);
      }));
      if (Util.isDefined(this.changeTab.value)) {
        this.activateTab(this.changeTab.value);
      }
    }

    if (Util.isDefined(this.selectedIndex) && this.selectedIndex !== this.activeIndex) {
      this.activateTab(this.selectedIndex);
    }
  }

  public activateTab(index: number): void {
    if (Util.isDefined(this.activeIndex) && Util.isDefined(this.activeTab)) {
      if (index === this.activeIndex) {
        return;
      }
      if (index > this.activeIndex) {
        this.activeTabBody.translate = -100;
        this.activeTab.active = false;
      }
      if (index < this.activeIndex) {
        this.activeTabBody.translate = 100;
        this.activeTab.active = false;
      }
    }

    if (Util.isDefined(this.tabBodys)) {
      this.tabBodys.forEach((tab: MrdTabBodyComponent, i: number) => {
        if (i < index) {
          tab.translate = -100;
        }
        if (i > index) {
          tab.translate = 100;
        }
      });

      let tabBody = this.tabBodys.get(index);
      let tab = this.tabs.get(index);

      // Frag mich nicht wieso, aber wenn die Tabs nicht über die Standard-Header, sondern über
      // selectedIndex oder changeTab gesetzt werden, dann ist der Content des TabBodys manchmal destroyed.
      // Deshalb setzen wir ihn hier wieder auf den Content des Tabs, der immer funktioniert.
      if (tabBody.content.destroyed) {
        tabBody.content = tab.content;
      }

      tab.active = true;
      tabBody.translate = 0;
      this.activeIndex = index;
      this.activeTabBody = tabBody;
      this.activeTab = tab;

      if (this.queryParameterActive) {
        this.router.navigate([], {
          queryParams: {
            [this.queryParameterActive]: index
          },
          queryParamsHandling: 'merge'
        });
      }

      if (!this.noHeader) {
        let header = this.tabHeader.get(index);
        this.tabUnderlineLeft = header.nativeElement.offsetLeft;
        this.tabUnderlineWidth = header.nativeElement.offsetWidth;
      }

      this.selectedTabChanged.emit(tab);
      this.selectedTabIndexChanged.emit(index);
    }
    this.cdr.detectChanges();
  }
}
