export function timeAttribute(value: string | number, fallbackValue?: string, defaultUnit?: string): string {
  if (typeof value === 'number') {
    return defaultUnit ? `${value}${defaultUnit}` : `${value}s`;
  }
  if (typeof value === 'string') {
    let numberValue: number = parseInt(value, 10);
    if (!isNaN(numberValue)) {
      return defaultUnit ? `${value}${numberValue}` : `${numberValue}s`;
    }
  }
  return value || fallbackValue || null;
}
