
export class ColorUtil {

  public static isHexColor(color: string): boolean {
    return /^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/.test(color);
  }

  public static isRgbColor(color: string): boolean {
    return /^rgb\((\d{1,3}), (\d{1,3}), (\d{1,3})\)$/.test(color);
  }

  public static isRgbaColor(color: string): boolean {
    return /^rgba\((\d{1,3}), (\d{1,3}), (\d{1,3}), (0|1|0\.\d+)\)$/.test(color);
  }

  public static hexToRgb(hex: string): { r: number, g: number, b: number } {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
      r: parseInt(result[1], 16),
      g: parseInt(result[2], 16),
      b: parseInt(result[3], 16)
    } : null;
  }

  public static hexToRgba(hex: string): { r: number, g: number, b: number, a: number } {
    if (ColorUtil.hexHasAlpha(hex)) {
      const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
      return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16),
        a: parseInt(result[4], 16) / 255
      } : null;
    } else {
      const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
      return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16),
        a: 1
      } : null;
    }
  }

  public static hexHasAlpha(hex: string): boolean {
    return /^#?([a-f\d]{8})$/i.test(hex);
  }

  public static rgbToHex(r: number, g: number, b: number, a?: number): string {
    return '#'+[r, g, b].map((x) => {
      const hex = x.toString(16);
      return hex.length === 1 ? '0' + hex : hex;
    }).join('');
  }

  public static rgbaToHex(r: number, g: number, b: number, a: number): string {
    a = !a ? 1 : a;
    let alpha = ((a * 255) | 1 << 8).toString(16).slice(1)
    return ColorUtil.rgbToHex(r, g, b) + alpha;
  }

  public static rgbStringToHex(rgb: string): string {
    const result = /^rgb\((\d{1,3}), (\d{1,3}), (\d{1,3})\)$/.exec(rgb);
    if (result) {
      return ColorUtil.rgbToHex(parseInt(result[1], 10), parseInt(result[2], 10), parseInt(result[3], 10));
    }
    return null;
  }

  public static rgbaStringToHex(rgba: string): string {
    const result = /^rgba\((\d{1,3}), (\d{1,3}), (\d{1,3}), (0|1|0\.\d+)\)$/.exec(rgba);
    if (result) {
      return ColorUtil.rgbaToHex(parseInt(result[1], 10), parseInt(result[2], 10), parseInt(result[3], 10), parseFloat(result[4]));
    }
    return null;
  }

  public static rgbaStringToRgb(rgba: string): { r: number, g: number, b: number, a: number } {
    const result = /^rgba\((\d{1,3}), (\d{1,3}), (\d{1,3}), (0|1|0\.\d+)\)$/.exec(rgba);
    if (result) {
      return {
        r: parseInt(result[1], 10),
        g: parseInt(result[2], 10),
        b: parseInt(result[3], 10),
        a: result[4] ? parseFloat(result[4]) : 1
      };
    }
    return null;
  }

  public static shouldTextBeDark(color: string, debug?: boolean): boolean {
    if (debug) {
      //debugger;
    }
    const rgba = ColorUtil.hexToRgba(color);
    if (rgba) {
      const { r, g, b, a } = rgba;
      const mixedColor = {
        r: r * a + (1 - a) * 255,
        g: g * a + (1 - a) * 255,
        b: b * a + (1 - a) * 255,
        a: 1
      };
      const brightness = Math.round(((mixedColor.r * 299) + (mixedColor.g * 587) + (mixedColor.b * 114)) / 1000);
      return brightness > 125;
    }
    return true;
  }

  public static changeColorOpacity(color: string, opacity: number): string {
    if (opacity > 1) {
      opacity /= 100;
    }

    if (ColorUtil.isRgbColor(color) || ColorUtil.isRgbaColor(color)) {
      let rgba = ColorUtil.rgbaStringToRgb(color);
      if (rgba) {
        return `rgba(${rgba.r}, ${rgba.g}, ${rgba.b}, ${rgba.a * opacity})`;
      }
      return color;
    }

    const rgb = ColorUtil.hexToRgba(color);
    if (rgb) {
      return `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${rgb.a * opacity})`;
    }
    return color;
  }

  public static changeColorBrightnessPercent(color: string, percent: number): string {
    let rgba;
    if (ColorUtil.isRgbColor(color) || ColorUtil.isRgbaColor(color)) {
      rgba = ColorUtil.rgbaStringToRgb(color);
    } else {
      rgba = ColorUtil.hexToRgba(color);
    }
    if (rgba) {
      if ([rgba.r, rgba.g, rgba.b].filter((c) => c === 0 || c === 255).length === 3) {
        rgba.r = rgba.r === 0 ? 1 : rgba.r;
        rgba.g = rgba.g === 0 ? 1 : rgba.g;
        rgba.b = rgba.b === 0 ? 1 : rgba.b;
      }
      return ColorUtil.rgbToHex(
        Math.min(255, Math.max(0, Math.round(rgba.r * (1 + percent / 100)))),
        Math.min(255, Math.max(0, Math.round(rgba.g * (1 + percent / 100)))),
        Math.min(255, Math.max(0, Math.round(rgba.b * (1 + percent / 100)))),
        rgba.a
      );
    }
    return color;
  }
}
