import { AfterViewInit, booleanAttribute, ChangeDetectionStrategy, ChangeDetectorRef, Component, ContentChildren, EventEmitter, Input, Output, QueryList } from '@angular/core';
import { BaseObject, SubscriptionHandler } from 'mrd-core';
import { colorAttribute, colorThemeAttribute } from './../../../../common/transforms/color-transform';
import { sizeAttribute } from './../../../../common/transforms/size-transform';
import { MrdButtonComponent } from './../../../mrd-button/components/mrd-button/mrd-button.component';
import { MrdConfigModel } from './../../../../common/model/config.model';
import { ConfigUtil } from './../../../../common/util/config.util';
import * as _ from 'underscore';

@Component({
  selector: 'mrd-button-toggle-group',
  templateUrl: './mrd-button-toggle-group.component.html',
  styleUrls: ['./mrd-button-toggle-group.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MrdButtonToggleGroupComponent extends BaseObject implements AfterViewInit {

  @ContentChildren(MrdButtonComponent) buttons: QueryList<MrdButtonComponent>;

  @Input({transform: booleanAttribute}) public rounded: boolean = false;
  @Input({transform: booleanAttribute}) public disabled: boolean = false;
  @Input({transform: booleanAttribute}) public multiple: boolean = false;

  /**
   * Gibt an, ob die ButtonGroup das Theme "primary" hat.
   *
   * Hierdurch wird die Hintergrundfarbe des Buttons auf die primäre Farbe des Themes gesetzt.
   *
   * @memberof MrdButtonComponent
   */
  @Input({transform: booleanAttribute}) public primary: boolean = false;

  /**
   * Gibt an, ob die ButtonGroup das Theme "accent" hat.
   *
   * Hierdurch wird die Hintergrundfarbe des Buttons auf die Akzentfarbe des Themes gesetzt.
   *
   * @memberof MrdButtonComponent
   */
  @Input({transform: booleanAttribute}) public accent: boolean = false;

  /**
   * Gibt an, ob die ButtonGroup das Theme "warn" hat.
   *
   * Hierdurch wird die Hintergrundfarbe des Buttons auf die Warnfarbe des Themes gesetzt.
   *
   * @memberof MrdButtonComponent
   */
  @Input({transform: booleanAttribute}) public warn: boolean = false;

  /**
   * Setzt die Grundfarbe des Buttons.
   *
   * Diese wird je nach Style des Buttons als Hintergrundfarbe oder Textfarbe verwendet.
   *
   * Es können Hex-, RGB- oder RGBA-Werte, sowie "primary", "accent" oder "warn" angegeben werden.
   *
   * @memberof MrdButtonComponent
   */
  @Input({alias: 'color', transform: colorThemeAttribute}) public customTextColor: string;

  /**
   * Setzt die Hintergrundfarbe des Buttons.
   *
   * Es können Hex-, RGB- oder RGBA-Werte angegeben werden.
   *
   * @memberof MrdButtonComponent
   */
  @Input({alias: 'backgroundColor', transform: colorAttribute}) public customBgColor: string;

  /**
   * Gibt an, ob die benutzerdefinierte Textfarbe nicht durch ein defniertes Thema überschrieben werden soll.
   *
   * @type {boolean}
   * @memberof MrdButtonComponent
   */
  @Input({transform: booleanAttribute}) public keepCustomTextColor: boolean = false;
  /**
   * Gibt an, ob die benutzerdefinierte Hintergrundfarbe nicht durch ein definiertes Thema überschrieben werden soll.
   *
   * @type {boolean}
   * @memberof MrdButtonComponent
   */
  @Input({transform: booleanAttribute}) public keepCustomBgColor: boolean = false;

  @Input({alias: 'unselectedBgColor', transform: colorAttribute}) public customToggleUnselectedColor: string;

  @Input({alias: 'unselectedTextColor', transform: colorAttribute}) public customToggleUnselectedTextColor: string;

  @Input({alias: 'selectedBgColor', transform: colorAttribute}) public customToggleSelectedColor: string;

  @Input({alias: 'selectedTextColor', transform: colorAttribute}) public customToggleSelectedTextColor: string;

  /**
   * Die Mindesthöhe des Buttons.
   *
   * @type {string | number}
   * @memberof MrdButtonComponent
   */
  @Input({transform: sizeAttribute}) public minHeight: string;
  /**
   * Die Schriftgröße des Buttons.
   *
   * @type {string | number}
   * @memberof MrdButtonComponent
   */
  @Input({transform: sizeAttribute}) public fontSize: string;

  /**
   * Der Radius der Ecken des Buttons.
   *
   * @type {string | number}
   * @memberof MrdButtonComponent
   */
  @Input({transform: sizeAttribute}) public borderRadius: string;

  /**
   * Das Klick-Event durch den Nutzer.
   *
   * @type {EventEmitter<Event>}
   * @memberof MrdButtonComponent
   */
  @Output() public indexChange: EventEmitter<number> = new EventEmitter<number>();


  /**
   * Die Konfiguration des Mrd-Buttons.
   *
   * @private
   * @type {MrdConfigModel}
   * @memberof MrdButtonComponent
   */
  private _config: MrdConfigModel = ConfigUtil.getConfig();

  public activeIndex: number = 0;

  constructor(
    protected cdr: ChangeDetectorRef
  ) {
    super();
  }

  ngAfterViewInit(): void {
    this.buttons.forEach((button: MrdButtonComponent, index: number) => {
      // button.flat = true;
      button.primary = this.primary;
      button.accent = this.accent;
      button.warn = this.warn;
      button.customTextColor ??= this.customTextColor;
      button.customBgColor ??= this.customBgColor;
      button.keepCustomTextColor ||= this.keepCustomTextColor;
      button.keepCustomBgColor ||= this.keepCustomBgColor;
      button.customToggleUnselectedColor ??= this.customToggleUnselectedColor;
      button.customToggleUnselectedTextColor ??= this.customToggleUnselectedTextColor;
      button.customToggleSelectedTextColor ??= this.customToggleSelectedTextColor;
      button.minHeight ??= this.minHeight;
      button.fontSize ??= this.fontSize;

      button.borderRadius = this.borderRadius ?? this.rounded ? '50px' : '4px';

      if (this.activeIndex === index) {
        button.toggleSelected = this.multiple ? !button.toggleSelected : true;
      }
      
      button.updateStyle();

      this.watch(button.click.asObservable(), new SubscriptionHandler((event: Event) => {
        event.stopPropagation();
        event.preventDefault();
        this.activeIndex = index;
        button.toggleSelected = this.multiple ? !button.toggleSelected : true;
        if (!this.multiple) {
          this.buttons.forEach((b, i) => {
            if (i !== index) {
              b.toggleSelected = false;
              b.updateStyle();
            }
          });
        }
        button.updateStyle();
        this.indexChange.emit(index);
        this.cdr.detectChanges();
      }));
    });
    this.cdr.detectChanges();
  }
}
