import { AfterViewInit, Directive, ElementRef, EventEmitter, Input, OnChanges, OnDestroy, Output, booleanAttribute } from '@angular/core';

@Directive({
  selector: '[hideIfTruncated]'
})
export class HideIfTruncatedDirective implements AfterViewInit, OnChanges, OnDestroy {

  @Input({transform: booleanAttribute}) hideIfTruncated: boolean = true;
  @Input({transform: booleanAttribute}) checkChildrenForAttribute: boolean = false;
  @Input() hideOnTruncatedElement: HTMLElement;
  @Input() requiredHideAttribute: string;
  @Input() displayState: string = 'block';
  @Input() parentResizeElement: HTMLElement;

  @Output() hiddenChanged: EventEmitter<boolean> = new EventEmitter<boolean>();

  private mutationObserver: MutationObserver;
  private resizeObserver: ResizeObserver;

  private blockNextResizeEvent: boolean = false;
  private resizeTimeout: any;

  constructor(
    private elementRef: ElementRef<HTMLElement>
  ) {}

  ngAfterViewInit(): void {
    if (this.hideIfTruncated) {
      this.mutationObserver = new MutationObserver(() => {
        this.ngOnChanges();
      });
      this.mutationObserver.observe(this.elementRef.nativeElement, {childList: true, subtree: true, characterData: true});

      if (this.parentResizeElement) {
        this.resizeObserver = new ResizeObserver((event) => {
          // if (this.blockNextResizeEvent) {
          //   this.blockNextResizeEvent = false;
          //   return;
          // }
          if (this.resizeTimeout) {
            clearTimeout(this.resizeTimeout);
          }
          this.elementRef.nativeElement.style.display = this.displayState;
          this.resizeTimeout = setTimeout(() => this.ngOnChanges(), 10);
        });
        this.resizeObserver.observe(this.parentResizeElement);
      }
    }
  }

  ngOnChanges(): void {
    if (this.hideIfTruncated) {

      const element = this.elementRef.nativeElement;
      if (element.scrollWidth <= element.clientWidth) {
        element.style.display = this.displayState;
        this.hiddenChanged.emit(false);
      } else {
        element.style.display = 'none';
        this.hiddenChanged.emit(true);
      }

      if (this.checkChildrenForAttribute) {
        // const element = this.elementRef.nativeElement;
        const children = element.children;
        for (let i = 0; i < children.length; i++) {
          if (children[i].getAttribute(this.requiredHideAttribute) !== null && (children[i] as HTMLElement).style) {
            if (this.hideOnTruncatedElement) {
              (children[i] as HTMLElement).style.display = this.hideOnTruncatedElement.style.display !== 'none' && this.hideOnTruncatedElement.scrollWidth <= this.hideOnTruncatedElement.clientWidth ? this.displayState : 'none';
            } else {
              (children[i] as HTMLElement).style.display = element.style.display === 'none' || children[i].scrollWidth <= children[i].clientWidth ? this.displayState : 'none';
            }
          }
        }
      }

      this.blockNextResizeEvent = true;
    }
  }

  ngOnDestroy(): void {
    if (this.mutationObserver) {
      this.mutationObserver.disconnect();
    }
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
    }
  }
}
