import { MrdConfigModel } from "../model/config.model";
import * as _ from 'underscore';

export class ConfigUtil {

  private static config: MrdConfigModel;

  private static customConfig: MrdConfigModel;

  public static setConfig(config: MrdConfigModel) {
    this.config = undefined;
    this.customConfig = config;
    this.getConfig();
  }

  public static getConfig() {
    if (this.config) {
      return this.config;
    }

    let defaultConfig = this.baseConfig;

    if (this.customConfig) {
      this.extendObject(defaultConfig, this.customConfig);
    }

    this.config = defaultConfig;
    return defaultConfig;
  }

  private static extendObject(obj: any, extObj: any): any {
    for (const [key, value] of Object.entries(extObj)) {
      if (_.isObject(value) && !_.isArray(value)) {
        obj[key] = this.extendObject(obj[key], value);
      } else {
        obj[key] = value;
      }
    };
    return obj;
  }

  private static get baseConfig(): MrdConfigModel {
    return {
      baseFont: {
        // size: "16px",
        family: "URW Din,Lato,sans-serif"
      },
      baseColors: {
        primary: "#68b022",
        accent: "#e7e7e7",
        warn: "#b00122",
        disabled: "#afa6a6"
      },
      formField: {
        borderRadius: "7px",
        borderRadiusRounded: "70px",
      },
      button: {
        backgroundColor: "transparent",
        textLightColor: "#ffffff",
        textDarkColor: "#000000",
        hoverColor: "#d3d3d361",
        activeColor: "#d3d3d3",
        disabled: {
          text: "#a6a6a6",
          background: "transparent"
        },

        border: "0 unset unset",
        borderRadius: "4px",

        minHeight: "36px",
        fontSize: "0.9em",
        iconSize: "1em",

        outline: {
          border: "1px solid #d3d3d3"
        },

        flat: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          }
        },
        raised: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          }
        },
        icon: {
          borderRadius: "50%",
          fontSize: "1em",
          diameter: "3em"
        },

        fab: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          },
          borderRadius: "50%",
          fontSize: "1em",
          diameter: "4em"
        },

        miniFab: {
          backgroundColor: "#ffffff",
          disabled: {
            text: "#a6a6a6",
            background: "#d3d3d3"
          },
          borderRadius: "50%",
          fontSize: "1em",
          diameter: "3em"
        },

        toggle: {
          backgroundColor: "#ffffff",
          unselectedBgColor: "#c8cac6",
        }
      },
      geoIcon: {
        width: "40px",
        height: "40px",
        margin: "5px",
        transitionTime: "1s",
        mainColor: "#000000",
        mainSelectedColor: "#ffffff",
        mainOpacity: 0.2,
        mainSelectedOpacity: 1,
        backColor: "#000000",
        backSelectedColor: "#ffffff",
        backOpacity: 0.2,
        backSelectedOpacity: 0.2,
        overlayColor: "#8ebf62",
        overlaySelectedColor: "#ffa500",
        overlayOpacity: 1,
        overlaySelectedOpacity: 1
      }
    }
  }
}
