import { ChangeDetectorRef, Directive, EmbeddedViewRef, Input, TemplateRef, ViewChild, ViewRef, booleanAttribute } from '@angular/core';


@Directive({
  selector: '[mrdTab]'
})
export class MrdTabComponent {

  @ViewChild(TemplateRef, {static: true}) _implicitContent: TemplateRef<any>;

  @Input("mrdTab") public set label(value: string) {
    this._label = value;
    this.cdr.detectChanges();
  }
  public get label(): string {
    return this._label;
  }
  private _label: string;

  private _embeddedViewRef: EmbeddedViewRef<any> | null = null;

  get content(): ViewRef | null {
    if (!this._embeddedViewRef || this._embeddedViewRef.destroyed) {
      this._embeddedViewRef = this.tr.createEmbeddedView({ $implicit: 'Angular', dateNow: new Date() });
    }
    return this._embeddedViewRef;
  }

  /**
   * Zero based index of the tab
   *
   * @memberof MrdTabComponent
   */
  public set index(value: number) {
    this._index = value;
    this.cdr.detectChanges();
  }
  public get index(): number {
    return this._index;
  }
  private _index: number;

  public set active(value: boolean) {
    this._active = value;
    this.cdr.detectChanges();
  }
  public get active(): boolean {
    return this._active;
  }
  private _active: boolean;

  @Input({alias: 'mrdTabPreserveContent',transform: booleanAttribute}) public preserveContent: boolean = false;


  constructor(
    private cdr: ChangeDetectorRef,
    readonly tr: TemplateRef<any>
  ) {}
}
