import { booleanAttribute, Component, Input, numberAttribute } from '@angular/core';
import { colorAttribute } from './../../../../common/transforms/color-transform';
import { sizeAttribute } from './../../../../common/transforms/size-transform';
import { timeAttribute } from './../../../../common/transforms/time-transform';
import { ConfigUtil } from './../../../../common/util/config.util';

/**
 * Komponente für die Darstellung von Geo-Icons (Schlagzeichnungen).
 *
 * @class MrdGeoIconComponent
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'mrd-geo-icon',
  templateUrl: './mrd-geo-icon.component.html',
  styleUrls: ['./mrd-geo-icon.component.scss']
})
export class MrdGeoIconComponent {

  private static _config = ConfigUtil.getConfig().geoIcon;

  /**
   * Breite des Icons.
   *
   * Wird eine Zahl übergeben, wird diese als Pixelwert interpretiert.
   *
   * @type {string | number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => sizeAttribute(value, MrdGeoIconComponent._config.width)})
  public width: string = MrdGeoIconComponent._config.width;

  /**
   * Höhe des Icons.
   *
   * Wird eine Zahl übergeben, wird diese als Pixelwert interpretiert.
   *
   * @type {string | number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => sizeAttribute(value, MrdGeoIconComponent._config.height)})
  public height: string = MrdGeoIconComponent._config.height;

  /**
   * Margin um das Icon.
   *
   * Wird eine Zahl übergeben, wird diese als Pixelwert interpretiert.
   *
   * @type {string | number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => sizeAttribute(value, MrdGeoIconComponent._config.margin)})
  public margin: string = MrdGeoIconComponent._config.margin;

  /**
   * Übergangszeit für Farbwechsel (Selektiert <-> nicht selektiert).
   *
   * Wird eine Zahl übergeben, wird diese als Sekundenwert interpretiert.
   *
   * @type {string | number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => timeAttribute(value, MrdGeoIconComponent._config.transitionTime)})
  public transitionTime: string = MrdGeoIconComponent._config.transitionTime;

  /**
   * Hauptfarbe des Icons.
   *
   * Es können Hex-, RGB- oder RGBA-Werte übergeben werden.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: string) => colorAttribute(value, MrdGeoIconComponent._config.mainColor)})
  public mainColor: string = MrdGeoIconComponent._config.mainColor;

  /**
   * Hauptfarbe des Icons, wenn es selektiert ist.
   *
   * Es können Hex-, RGB- oder RGBA-Werte übergeben werden.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: string) => colorAttribute(value, MrdGeoIconComponent._config.mainSelectedColor)})
  public mainSelectedColor: string = MrdGeoIconComponent._config.mainSelectedColor;

  /**
   * Deckkraft der Hauptfarbe des Icons.
   *
   * Werte zwischen 0 und 1 sind möglich.
   *
   * @type {number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => numberAttribute(value, MrdGeoIconComponent._config.mainOpacity)})
  public mainOpacity: number = MrdGeoIconComponent._config.mainOpacity;

  /**
   * Deckkraft der Hauptfarbe des Icons, wenn es selektiert ist.
   *
   * Werte zwischen 0 und 1 sind möglich.
   *
   * @type {number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => numberAttribute(value, MrdGeoIconComponent._config.mainSelectedOpacity)})
  public mainSelectedOpacity: number = MrdGeoIconComponent._config.mainSelectedOpacity;

  /**
   * Farbe der 2. Ebene des Icons.
   *
   * Es können Hex-, RGB- oder RGBA-Werte übergeben werden.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: string) => colorAttribute(value, MrdGeoIconComponent._config.overlayColor)})
  public overlayColor: string = MrdGeoIconComponent._config.overlayColor;

  /**
   * Farbe der 2. Ebene des Icons, wenn es selektiert ist.
   *
   * Es können Hex-, RGB- oder RGBA-Werte übergeben werden.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: string) => colorAttribute(value, MrdGeoIconComponent._config.overlaySelectedColor)})
  public overlaySelectedColor: string = MrdGeoIconComponent._config.overlaySelectedColor;

  /**
   * Deckkraft der 2. Ebene des Icons.
   *
   * Werte zwischen 0 und 1 sind möglich.
   *
   * @type {number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => numberAttribute(value, MrdGeoIconComponent._config.overlayOpacity)})
  public overlayOpacity: number = MrdGeoIconComponent._config.overlayOpacity;

  /**
   * Deckkraft der 2. Ebene des Icons, wenn es selektiert ist.
   *
   * Werte zwischen 0 und 1 sind möglich.
   *
   * @type {number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => numberAttribute(value, MrdGeoIconComponent._config.overlaySelectedOpacity)})
  public overlaySelectedOpacity: number = MrdGeoIconComponent._config.overlaySelectedOpacity;

  /**
   * Hintergrundfarbe des Icons, wenn es eine zweite Ebene gibt.
   *
   * Es können Hex-, RGB- oder RGBA-Werte übergeben werden.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: string) => colorAttribute(value, MrdGeoIconComponent._config.backColor)})
  public backColor: string = MrdGeoIconComponent._config.backColor;

  /**
   * Hintergrundfarbe des Icons, wenn es eine zweite Ebene gibt und diese selektiert ist.
   *
   * Es können Hex-, RGB- oder RGBA-Werte übergeben werden.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: string) => colorAttribute(value, MrdGeoIconComponent._config.backSelectedColor)})
  public backSelectedColor: string = MrdGeoIconComponent._config.backSelectedColor;

  /**
   * Deckkraft des Hintergrunds des Icons.
   *
   * Werte zwischen 0 und 1 sind möglich.
   *
   * @type {number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => numberAttribute(value, MrdGeoIconComponent._config.backOpacity)})
  public backOpacity: number = MrdGeoIconComponent._config.backOpacity;

  /**
   * Deckkraft des Hintergrunds des Icons, wenn es selektiert ist.
   *
   * Werte zwischen 0 und 1 sind möglich.
   *
   * @type {number}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: (value: any) => numberAttribute(value, MrdGeoIconComponent._config.backSelectedOpacity)})
  public backSelectedOpacity: number = MrdGeoIconComponent._config.backSelectedOpacity;

  /**
   * Gibt an, ob das Icon selektiert ist.
   *
   * @type {boolean}
   * @memberof MrdGeoIconComponent
   */
  @Input({transform: booleanAttribute}) public isSelected: boolean = false;

  /**
   * Daten für die Basis des Icons.
   *
   * @type {any[]}
   * @memberof MrdGeoIconComponent
   */
  @Input() public set baseData(value: any[]){
    this._baseData = value;
    let path = this.getPathString(value);
    this.base = path.ps;
    this.viewBox = path.vbs;
  }

  public get baseData(): any[]{
    return this._baseData;
  }

  private _baseData: any[];

  /**
   * Daten für die 2. Ebene des Icons.
   *
   * @type {any[]}
   * @memberof MrdGeoIconComponent
   */
  @Input() public set overlayData(value: any[]){
    this._baseData = value;
    let p = this.getPathString(value);
    this.overlay = p.ps;

    if(!value) {
      this.hasOverlay = false;
    } else {
      this.hasOverlay = true;
    }
  }

  public get overlayData():any[]{
    return this._baseData;
  }

  /**
   * Gibt an, ob das Icon eine 2. Ebene hat.
   *
   * @type {boolean}
   * @memberof MrdGeoIconComponent
   */
  public hasOverlay:boolean;
  /**
   * Beinhaltet die Daten der ersten Ebene des Icons.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  public base: string;
  /**
   * Beinhaltet die Daten der zweiten Ebene des Icons.
   *
   * @type {string}
   * @memberof MrdGeoIconComponent
   */
  public overlay: string;
  public viewBox: string;

  private getPathString(d): any{
    if(!d) {
      return {ps:"", vb: [0,0,0,0], vbs: "0 0 1 1"};
    }

    // Wenn der zweite Level ein Array ist Handelt es sich um daten mit inselflaechen.
    // Dann wird die erste Flaeche verwendet ohne inseln
    if(Array.isArray(d) && Array.isArray(d[0]) && Array.isArray(d[0][0])) {
       d = d[0];
    }

    var vb = [0,0,0,0];

    var ps;
    for(let i = 0;i < d.length; i++) {

      if(!ps) {
        ps = "M";
      } else {
        ps+= "L";
      }

      let dd2 = this.mercEncode(d[i][1],d[i][0],100,100);
      let d1 = dd2[0] * 30000;
      let d2 = dd2[1] * 30000;


      if (vb[0] == 0 || vb[0]>d1) {
        vb[0] = d1;
      }

      if (vb[1] == 0 || vb[1]>d2) {
        vb[1] = d2;
      }

      if (vb[2] == 0 || vb[2]<d1) {
        vb[2] = d1;
      }

      if (vb[3] == 0 || vb[3]<d2) {
        vb[3] = d2;
      }

      ps += d1 +" " + d2;
    }


    ps += "z";
    return {ps:ps, vb:vb, vbs: vb[0] + " " + vb[1] + " " + (vb[2]-vb[0]) + " " +(vb[3]-vb[1])  };
  }

  mercEncode(lat, lng, w, h){
    // get x
    var x = (lng + 180) * (w / 360);
    // convert from degrees to radians
    var latRad = lat * Math.PI / 180;
    // get y value
    var mercN = Math.log(Math.tan((Math.PI / 4) + (latRad / 2)));
    var y = (h / 2) - (w * mercN / (2 * Math.PI));
    return [x,y];
  }
}
