import { ColorUtil } from './../../../../common/util/color.util';
import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, numberAttribute } from '@angular/core';
import { MrdConfigModel } from './../../../../common/model/config.model';
import { ConfigUtil } from './../../../../common/util/config.util';

@Component({
  selector: 'mrd-progress-bar',
  templateUrl: './mrd-progress-bar.component.html',
  styleUrls: ['./mrd-progress-bar.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MrdProgressBarComponent implements AfterViewInit {

  private config: MrdConfigModel = ConfigUtil.getConfig();

  @Input() public set mode(mode: 'determinate' | 'indeterminate') {
    this._mode = mode;
  }
  public get mode(): 'determinate' | 'indeterminate' {
    return this._mode;
  }
  private _mode: 'determinate' | 'indeterminate' = 'indeterminate';

  @Input({transform: numberAttribute}) public set value(value: number) {
    this._value = Math.max(0, Math.min(100, value || 0));
  }
  public get value(): number {
    return this.mode === 'determinate' ? this._value - 100 : -100;
  }
  private _value: number = 0;

  @Input() public set color(color: string) {
    if (color === 'primary') {
      color = this.config.baseColors.primary as string;
    }
    if (color === 'accent') {
      color = this.config.baseColors.accent as string;
    }
    if (color === 'warn') {
      color = this.config.baseColors.warn as string;
    }
    this._color = color;
    this._bgColor = ColorUtil.changeColorOpacity(color, 20);
  }
  public get color(): string {
    return this._color;
  }
  private _color: string = this.config.baseColors.primary as string;
  private _bgColor: string = ColorUtil.changeColorOpacity(this.config.baseColors.primary as string, 20);

  public get bgColor(): string {
    return this._bgColor;
  }

  constructor(
    private cdr: ChangeDetectorRef
  ) {}

  public ngAfterViewInit(): void {
    this.cdr.detectChanges();
  }
}
