import { Component, Input, OnInit, TemplateRef } from '@angular/core';

@Component({
  selector: 'mrd-tooltip',
  templateUrl: './mrd-tooltip.component.html',
  styleUrls: ['./mrd-tooltip.component.scss']
})
export class MrdTooltipComponent implements OnInit {

  /**
   * This is simple text which is to be shown in the tooltip
   */
   @Input() text: string;

   /**
    * This provides finer control on the content to be visible on the tooltip
    * This template will be injected in ToolTipRenderer directive in the consumer template
    * <ng-template #template>
    *  content.....
    * </ng-template>
    */
   @Input() contentTemplate: TemplateRef<any>;

   @Input() defaultStyle: boolean = true;

   constructor() { }

   ngOnInit() {
   }
}
