import { AfterViewInit, Component, Input, OnDestroy, booleanAttribute, numberAttribute } from '@angular/core';
import { MrdConfigModel } from './../../../../common/model/config.model';
import { ConfigUtil } from './../../../../common/util/config.util';

@Component({
  selector: 'mrd-progress-spinner',
  host: {
    '[style.width.px]': 'diameter',
    '[style.height.px]': 'diameter',
    '[style.margin-bottom.em]': 'showLabel ? 1.5 : 0'
  },
  templateUrl: './mrd-progress-spinner.component.html',
  styleUrls: ['./mrd-progress-spinner.component.scss']
})
export class MrdProgressSpinnerComponent implements AfterViewInit, OnDestroy {

  private config: MrdConfigModel = ConfigUtil.getConfig();

  @Input() public mode: 'determinate' | 'indeterminate' = 'indeterminate';

  @Input() public set color(color: string) {
    if (color === 'primary') {
      color = this.config.baseColors.primary as string;
    }
    if (color === 'accent') {
      color = this.config.baseColors.accent as string;
    }
    if (color === 'warn') {
      color = this.config.baseColors.warn as string;
    }
    this._color = color;
  }
  public get color(): string {
    return this._color;
  }
  private _color: string = this.config.baseColors.primary as string;

  @Input({transform: numberAttribute})
  get value(): number {
    return this.mode === 'determinate' ? this._value : 0;
  }
  set value(v: number) {
    this._value = Math.max(0, Math.min(100, v || 0));
  }
  private _value = 0;

  @Input({transform: numberAttribute})
  get diameter(): number {
    return this._diameter;
  }
  set diameter(size: number) {
    this._diameter = size || 0;
  }
  private _diameter = 100;

  @Input({transform: numberAttribute})
  get strokeWidth(): number {
    return this._strokeWidth ?? this.diameter / 10;
  }
  set strokeWidth(value: number) {
    this._strokeWidth = value || 0;
  }
  private _strokeWidth: number;

  @Input({transform: booleanAttribute}) public showLabel: boolean = false;

  @Input() public set labelText(label: string) {
    this._labelText = label;
  }
  public get labelText(): string {
    return this._labelText || '';
  }
  private _labelText: string = 'Lade Daten';

  public dots: string = '';
  private interval: any;

  public get circleRadius(): number {
    return (this.diameter - this.strokeWidth) / 2;
  }

  public get strokeCircumference(): number {
    return 2 * Math.PI * this.circleRadius;
  }

  public get strokeDashOffset() {
    if (this.mode === 'determinate') {
      return (this.strokeCircumference * (100 - this._value)) / 100;
    }
    return null;
  }

  public get circleStrokeWidth() {
    if (this.diameter === 110) {
      debugger;
    }
    return (this.strokeWidth / this.diameter) * 100;
  }

  public get viewBox() {
    const viewBox = this.circleRadius * 2 + this.strokeWidth;
    return `0 0 ${viewBox} ${viewBox}`;
  }

  ngAfterViewInit(): void {
    if (this.showLabel) {
      this.interval = setInterval( () => {
        if (this.dots.length === 3) {
          this.dots = '';
        } else {
          this.dots = this.dots + '.';
        }
      }, 700);
    }
  }

  ngOnDestroy(): void {
    if (this.interval) {
      clearInterval(this.interval);
    }
  }
}
